ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

li {
  float: left;
  text-decoration: none;
}

li a {
  display: block;
  padding: 8px;
  color: #F4EBEE;
  background-color: #E21B4D;
  text-decoration: none;
}

li a:hover {
  background-color: #FFD300;
  color: #000B8D;
}

.Nav {
  width: 100%;
  Height: 35px;
  background-color: #000B8D;
}

.home, .shop {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translateY(-50%) translateX(-50%);
  transform: translateY(-50%) translateX(-50%);
  max-width: 100%;
  background-color: white;
}

.shop {
  display: flex;
  flex-direction: row;
}

.products {
  display: flex;
  flex-direction: row;
}

.product {
  display: flex;
  flex-direction: column;

  padding: 25px;
}

.product-img {
  padding: 0px 0px 0px 0px;
  width:180px; /* you can use % */
  height: auto;
}

.fas {
  padding: 4px;
}

.fas:hover {
  cursor: pointer;
  color: #E21B4D;
}

.row {
  display: flex;
  flex-direction: row;
}

.fa-cart-arrow-down {
  text-align: right;
}

.right-align {
  display: flex;
  flex-direction: row-reverse;
  margin: 5px;
}

.product h3 {
  height: 40px;
}

label {
  padding-top: 20px;
}





.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
